.QuestionDetails {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden;
  min-height: 1px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.QuestionDetails .Panel__in {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  overflow: hidden;
  min-height: 1px;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.QuestionDetails--content {
  flex-grow: 1;
  overflow-y: scroll;
  pointer-events: visible;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
.QuestionDetails__answersGroup {
  margin-top: 20px;
}
.QuestionDetails__answer {
  margin-top: 10px;
}
.QuestionDetails__generalInfo--card-dark {
  background: var(--content_tint_background);
}
.QuestionDetails .TabsItem:first-child {
  padding-left: 0;
}
.QuestionDetails .TabsItem:first-child::after {
  left: 0;
  width: calc(100% - 16px);
}
.QuestionDetails .TabsItem:last-child {
  padding-right: 0;
}
.QuestionDetails .TabsItem:last-child::after {
  right: 0;
  width: calc(100% - 16px);
}
